import React from 'react';
import { Link } from "react-router-dom";
import { Authorized, NotAuthorized } from './AuthorizedContent';
import { OpenGuid } from "./Guid";
import Pure from './Pure';
import "./SideNavi.css";

class SideNavi extends Pure {
    render() {
        return <>
            <div className="side_navi">
                <p>
                    <Authorized>
                        <Link to="/Home"> </Link>
                        <br />
                        <Link to="#" onClick={OpenGuid()}> </Link>
                        <Link to="/Profile"> </Link>
                        {/* <Link to="/Rules"></Link> */}
                        <br />
                        <Link to="/Logout"><b> </b></Link>
                    </Authorized>
                    <NotAuthorized>
                        <Link to="/Home"> </Link>
                        <br />
                        <Link to="/Login"><b> </b></Link>
                        <Link to="/Register"><b> </b></Link>
                        <br />
                        <Link to="#" onClick={OpenGuid()}> </Link>
                        <Link to="/Rules"></Link>
                    </NotAuthorized>
                </p>
            </div>
        </>;
    }
}

export default SideNavi;